@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
*{
    font-family: 'Roboto';
}

.AnimationShow{
    animation: mostrar 0.3s;    

}

@keyframes mostrar { 
    from{
        transform: scale(1.3);
        opacity: 0;
    }

    to {
        opacity: 1;
        transform: scale(1);
    }


}
.page_loader{
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.page_loader span{
    width: 50px;
    height: 50px;
    border: 6px solid rgba(140, 168, 157, 0.459);
    border-left: 6px solid #1972D2;
    border-radius: 50%;
    animation: page_loader 1s infinite linear;
}


@keyframes page_loader {
    to {
        transform: rotate(360deg);
    }
}
